import React, { useState, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import Fab from '@material-ui/core/Fab'
import Modal from '@material-ui/core/Modal'
import Zoom from '@material-ui/core/Zoom'
import { fade } from '@material-ui/core/styles/colorManipulator'

// import useNewImageMenu from '../PhotoSeries/views/controls/useNewImageMenu'
import AddCameraIcon from '@material-ui/icons/AddAPhoto'
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => {
  const pulseCol = fade(theme.palette.secondary.light, 0.4)
  return {
    root: {
    },
    fab: {
      background: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
      '&::before': {
        content: '"  "',
        display: 'block',
        position: 'absolute',
        width: '100%',
        height: '100%',
        borderRadius: 'inherit',
        boxShadow: `0 0 0 0 ${pulseCol}`,
        animation: '$fabPulse 4s',
        animationIterationCount: 5,
        animationTimingFunction: 'linear'
      }
    },
    '@keyframes fabPulse': {
      '0%': {
        boxShadow: `0 0 0 0px ${pulseCol}`
      },
      '90%': {
        boxShadow: `0 0 0 0px ${pulseCol}`
      },
      '100%': {
        boxShadow: `0 0 0 10px transparent`
      }
    },
    directionUp: {
    },
    actionsClosed: {
      display: 'none'
    },
    modalContainer: {
      position: 'absolute',
      bottom: '0%',
      left: '50%',
      transformOrigin: '50% 50%',
      transform: 'translateY(-50%) translateX(-50%)'
    },
    subFabContainer: {
      padding: theme.spacing(2)
    },
    subFab: {
      // display: 'block',
      // padding: theme.spacing(4)
    },
    subFabIcon: {
      margin: theme.spacing(1)
    }
  }
})

const AddFab = ({ className, children, onCameraClick, onLibraryClick, isIos }) => {
  const [open, setOpen] = useState(false)
  const [down, setDown] = useState(false)

  const timerRef = useRef()
  const classes = useStyles()
  // const theme = useTheme()
  const downRef = useRef(down)
  const elRef = useRef()
  downRef.current = down

  // Make sure to clean up the timer
  useEffect(() => () => {
    // clean up the timer if we no longer need it
    timerRef.current = null
    downRef.current = null
  }, [])

  const startTimer = (el) => {
    if (isIos) return
    if (!elRef.current) return

    setDown(true)
    if (!open && !timerRef.current) {
      timerRef.current = setTimeout(() => {
        if (downRef.current) setOpen(true)
        timerRef.current = null
      }, 300)
    }
  }

  const onDown = (e) => {
    // console.log('onDown', e.currentTarget)
    // setSeries(series)
    startTimer(e.currentTarget)
  }

  const block = (e) => {
    if (e && e.stopPropagation) {
      e.stopPropagation()
      e.preventDefault()
    }
  }

  const cameraAction = (e) => {
    console.log('cameraclick')
    setOpen(false)
    setDown(false)
    onCameraClick()
    block(e)
  }
  const libraryAction = (e) => {
    console.log('libraryClick')
    setOpen(false)
    setDown(false)
    onLibraryClick()
    block(e)
  }

  return (
    <div className={classNames(classes.rootContainer, className)} ref={elRef}>
      <Fab
        className={classes.fab}
        onMouseDown={onDown}
        // onMouseUp={onUp}
        onTouchStart={onDown}
        // onTouchEnd={onUp}
        onClick={(e) => {
          block(e)
          setDown(false)
          if (!open) {
            console.log('cameraclick MAIN')
            onCameraClick()
          }
        }}
        disableFocusRipple
        disableTouchRipple
        disableRipple
        size='large'
        color='secondary'
        aria-label='Add'
      >
        {children}
      </Fab>
      <Modal
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
        open={open}
        onClose={() => setOpen(false)}
      >
        <div className={classes.modalContainer}>
          <Zoom
            in={open}
            timeout={500}
            unmountOnExit
          >
            <div className={classes.subFabContainer}>
              <Fab
                className={classes.subFab}
                disableFocusRipple
                variant='extended'
                size='large'
                color='secondary'
                aria-label='Add'
                onClick={() => {
                  setOpen(false)
                  libraryAction()
                }}
              >
                <PhotoLibraryIcon color='inherit' className={classes.subFabIcon} />
              Library
              </Fab>
            </div>
          </Zoom>
          <Zoom
            in={open}
            timeout={250}
            // style={{
            //   transitionDelay: `${open ? 500 : 0}ms`
            // }}
            unmountOnExit
          >
            <div className={classes.subFabContainer}>
              <Fab
                className={classes.subFab}
                disableFocusRipple
                variant='extended'
                size='large'
                color='secondary'
                aria-label='Add'
                onClick={() => {
                  setOpen(false)
                  cameraAction()
                }}
              >
                <AddCameraIcon color='inherit' className={classes.subFabIcon} />
              Camera
              </Fab>
            </div>
          </Zoom>
        </div>
      </Modal>
    </div>

  )
}

AddFab.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  isIos: PropTypes.bool,
  onCameraClick: PropTypes.func.isRequired,
  onLibraryClick: PropTypes.func.isRequired
}

export default AddFab
