// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { watchUpdate } from './updateQueries'
import { watchSeries } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'

const withMutation = graphql(watchSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      watchSeriesMutation: () => {
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const variables = {
          seriesId: ownProps.activeSeries
        }
        const optimisticResponse = {
          watchSeries: ownProps.activeSeries
        }
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, watchSeries)

        return mutate({
          variables,
          optimisticResponse,
          update: updateThunk()
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('watchSeriesMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  watchUpdate(store, data.watchSeries)
}

export default {
  withMutation,
  updateThunk
}
