// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { allSeriesUpdate, feedUpdate } from './updateQueries'
import { deleteSeries } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'

const withMutation = graphql(deleteSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      deleteSeriesMutation: () => {
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const variables = {
          id: ownProps.activeSeries
        }
        const optimisticResponse = {
          deleteSeries: ownProps.activeSeries
        }
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, deleteSeries)

        return mutate({
          variables,
          optimisticResponse,
          update: updateThunk({}, ownProps.onAfterSeriesDelete)
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('deleteSeriesMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  allSeriesUpdate(store, { id: data.deleteSeries }, true)
  feedUpdate(store, { id: data.deleteSeries }, null, true)

  if (cb) cb(data.createSeries.series.id)
}

export default {
  withMutation,
  updateThunk
}
