import { getWholeSeries, allSeries, userSeries, getMyFeed, getUser } from '../queries'
import { seriesKeys, seriesSummaryKeys, photoSummaryKeys } from '../fragments'
import _ from 'lodash'

/**********************************************************
 *
 * NOTE:
 *
 * The methods in this file are for modifying the Apollo cache
 * store. This ensures interoperability between queries and
 * helps us rewind and replay mutations after an app reload
 *
 *
 **********************************************************/

/**
 * Update a series in the cached getWholeSeries queries
 *
 * @param {*} store
 * @param {*} photo
 * @param {*} remove
 */
export const getWholeSeriesUpdate = (store, series) => {
  const variables = { id: series.id }
  const query = getWholeSeries
  let newData = {
    series: {}
  }
  try {
    const qVal = store.readQuery({ query, variables })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.error('ERROR: getWholeSeriesUpdate readQuery failed')
  }
  try {
    newData.series = { ...newData.series, ...series }
    store.writeQuery({ query, variables, data: newData })
  }
  catch (e) {
    console.error('ERROR: getWholeSeriesUpdate writeQuery failed')
  }
}

/**
 * Add or remove a photo a full getWholeSeries cached query
 *
 * @param {*} store
 * @param {*} photo
 * @param {*} remove
 */
export const getWholeSeriesPhotoUpdate = (store, photo, remove = false) => {
  const variables = { id: photo.seriesId }
  const query = getWholeSeries
  try {
    // This may or may not exist yet
    const qVal = store.readQuery({ query, variables })
    const newData = _.cloneDeep(qVal)
    if (remove) {
      newData.series.photos = newData.series.photos.filter(ph => ph.id === photo.id)
    }
    else {
      const idx = newData.series.photos.findIndex(ph => ph.id === photo.id)
      if (idx > -1) {
        newData.series.photos = [...newData.series.photos]
        newData.series.photos[idx] = photo
      }
      else {
        newData.series.photos = [...newData.series.photos, photo]
      }
    }
    store.writeQuery({ query, variables, data: newData })
  }
  catch (e) {
    console.error('ERROR: allSeriesUpdate readQuery failed  ')
  }
}

/**
 * HELPER Function. Help us add and remove one item from
 * any of the allSeries arrays in the cache
 *
 * @param {*} origArr
 * @param {*} series
 * @param {*} remove
 */
function updateSeriesSummaries (origArr, series, remove = false) {
  let newArr = []
  if (remove) {
    newArr = origArr.filter(s => s.id !== series.id)
  }
  else {
    const newAllSeriesEntry = _.pick(series, seriesKeys)
    newArr = [...origArr, {
      ...newAllSeriesEntry, seriesStatus: null, __typename: 'SeriesSummary'
    }]
  }
  return newArr
}

/**
 * HELPER Function. Help us add and remove one feedItem from the cached array
 *
 * @param {*} origArr
 * @param {*} series
 * @param {*} photo
 * @param {*} remove
 */
function updateFeedList (origArr, series, photo, remove = false) {
  let newArr = []
  if (remove) {
    // remove the item
    newArr = origArr.filter(fItem => {
      if (series && fItem.series.id === series.id) return false
      else if (photo && fItem.photo.id === photo.id) return false
      else return true
    })
  }
  else {
    // Maybe this is just an update
    const idx = origArr.findIndex(fitem => fitem.series.id === photo.seriesId)

    let newSeriesSummary = series ? _.pick(series, seriesSummaryKeys) : null
    const newPhotoSummary = _.pick(photo, photoSummaryKeys)

    // Update the item
    if (idx > -1) {
      newArr = [...origArr]
      newArr[idx].photo = newPhotoSummary
    }
    // Insert a new item
    else {
      newArr = [...origArr, {
        _id: newPhotoSummary.id,
        series: { ...newSeriesSummary, __typename: 'SeriesSummary' },
        photo: { ...newPhotoSummary, __typename: 'PhotoSummary' },
        relation: 'OWNED',
        __typename: 'SeriesSummary'
      }]
    }
  }
  return newArr
}

/**
 * Update a changed Series in the allSeries cache
 *
 * @param {*} store
 * @param {*} series
 * @param {*} remove
 */
export const allSeriesUpdate = (store, series, remove = false) => {
  const variables = { limit: 100 }
  const query = allSeries
  let newData = {
    allSeries: { seriesSummaries: [] },
    nextToken: null,
    __typename: 'PaginatedSeries'
  }
  try {
    // This may or may not exist yet
    const qVal = store.readQuery({ query, variables })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.error('ERROR: allSeriesUpdate readQuery failed')
  }

  try {
    newData.allSeries.seriesSummaries = updateSeriesSummaries(newData.allSeries.seriesSummaries, series, remove)
    store.writeQuery({ query, variables, data: newData })
  }
  catch (e) {
    console.error('ERROR: allSeriesUpdate writeQuery failed')
  }
}

/**
 * Add or remove a photot to the allSeries cached query
 *
 * @param {*} store
 * @param {*} series
 * @param {*} photo
 * @param {*} remove
 */
export const allSeriesPhotoUpdate = (store, series, photo, remove = false) => {
  const query = allSeries
  try {
    const qVal = store.readQuery({ query, variables: { limit: 100 } })
    const newData = _.cloneDeep(qVal)

    newData.allSeries.seriesSummaries = newData.allSeries.seriesSummaries.map(s => {
      if (s.id !== photo.seriesId) return s
      else {
        const newS = _.cloneDeep(s)
        if (remove && series) {
          // set a new latest photo
          if (newS.latest.id === photo.id) {
            newS.latest = series.photos[series.photos.length - 1]
          }
          // set a new featured photo
          if (newS.featured.id === photo.id) {
            newS.featured = series.photos[series.photos.length - 1]
          }
        }
        else {
          // set a new latest photo
          const photoSummary = _.pick(photo, photoSummaryKeys)
          photoSummary.__typename = 'PhotoSummary'
          newS.latest = photoSummary
        }
        return newS
      }
    })
    store.writeQuery({ query, data: newData })
  }
  catch (e) {
    console.error('createPhotoMutation:: allSeries ERROR')
  }
}

/**
 * Add or remove a series to the user's listing
 *
 * @param {*} store
 * @param {*} series
 * @param {*} remove
 */
export const userSeriesUpdate = (store, series, remove = false) => {
  const query = userSeries

  let newData = {
    userSeries: { seriesSummaries: [] },
    nextToken: null,
    __typename: 'PaginatedSeries'
  }
  try {
    // This may or may not exist yet
    const qVal = store.readQuery({ query })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.error('ERROR: userSeriesUpdate readQuery failed')
  }

  try {
    newData.userSeries.seriesSummaries = updateSeriesSummaries(newData.userSeries.seriesSummaries, series, remove)
    store.writeQuery({ query, data: newData })
  }
  catch (e) {
    console.error('ERROR: userSeriesUpdate writeQuery failed')
  }
}

/**
 * Add or remove an item to the cached feed
 * This is really limited because we can't add anything we don't already have in the series cache
 *
 * @param {*} store
 * @param {*} series
 * @param {*} photo
 * @param {*} remove
 */
export const feedUpdate = (store, series, photo, remove = false) => {
  const query = getMyFeed

  let newData = {
    myFeed: { feed: [] },
    window: null,
    __typename: 'PaginatedSeries'
  }
  try {
    // This may or may not exist yet
    const qVal = store.readQuery({ query })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.error('ERROR: feedUpdate readQuery failed')
  }

  try {
    newData.myFeed.feed = updateFeedList(newData.userSeries.seriesSummaries, series, photo, remove)
    store.writeQuery({ query, data: newData })
  }
  catch (e) {
    console.error('ERROR: feedUpdate writeQuery failed')
  }
}

/**
 * Watch or unwatch the series in the cached getUser query
 * @param {*} store
 * @param {*} seriesId
 * @param {*} remove
 */
export const watchUpdate = (store, seriesId, remove = false) => {
  const query = getUser
  try {
    const qVal = store.readQuery({ query })
    const newData = _.cloneDeep(qVal)

    if (remove) {
      newData.user.watching = newData.user.watching.filter(sid => sid !== seriesId)
    }
    else {
      newData.user.watching = [...new Set([...newData.user.watching || [], seriesId])]
    }

    store.writeQuery({ query, data: newData })
  }
  catch (e) {
    console.error('ERROR: getUser readQuery failed')
  }
}
