import React, { useState, useContext, useMemo, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import actions from '../redux/actions'
import Bottleneck from 'bottleneck'
import { AppContext } from '../contexts'
import { home } from '../config'
import { graphql } from 'react-apollo'
import { getWholeSeries, geoSeriesIdOnly } from '../graphql/queries'
import { long2tile, lat2tile } from 'geotilehash'
import OfflineMap, { STATES } from '../components/maps/OfflineMap'

const OfflineMapConnect = ({
  onMenuBack,
  lastMapViewport, completedDownload, viewport, setOfflineCompleted, setOfflineViewport
}) => {
  const [toolState, setToolState] = useState(viewport && completedDownload ? STATES.LOCKED : STATES.NAVIGATING)
  const limiterRef = useRef()
  const { online } = useContext(AppContext)

  // Setting the bounds is what kicks off the whole process
  const [bounds, setBounds] = useState(null)
  // Series ids only get set when the geoQuery finishes
  const [seriesIds, setSeriesIds] = useState(null)
  // This is the current Series object we are putting through the query
  const [activeSeriesId, setActiveSeriesId] = useState(null)
  const [seriesPhotos, setSeriesPhotos] = useState([])

  const [tileTick, setTileTick] = useState([0, 0])
  const [seriesTick, setSeriesTick] = useState([0, 0])
  const [seriesPhotoTick, setSeriesPhotoTick] = useState([0, 0])

  // We're dealing in asynchronous so we need a ref to make sure we're usign the current values
  const tileTickRef = useRef()
  tileTickRef.current = { tileTick, seriesTick, seriesPhotoTick, seriesIds, seriesPhotos }

  const onTileTick = () => setTileTick([tileTickRef.current.tileTick[0] + 1, tileTickRef.current.tileTick[1]])
  const onTileAdd = (num) => setTileTick([tileTickRef.current.tileTick[0], tileTickRef.current.tileTick[1] + num])
  const onSeriesPhotoTick = () => {
    setSeriesPhotoTick([tileTickRef.current.seriesPhotoTick[0] + 1, tileTickRef.current.seriesPhotoTick[1]])
  }
  const onSeriesPhotoAdd = (num) => setSeriesPhotoTick([tileTickRef.current.seriesPhotoTick[0], tileTickRef.current.seriesPhotoTick[1] + num])

  const onSeriesTick = (urls) => {
    const theTick = tileTickRef.current.seriesTick
    const theIds = tileTickRef.current.seriesIds.ids
    const thePhotos = tileTickRef.current.seriesPhotos
    if (urls) {
      let addCount = 0
      urls.map(url => {
        if (thePhotos.indexOf(url) === -1) {
          addCount++
          setSeriesPhotos([...thePhotos, url])
          limiterRef.current.schedule(() => makeRequest('GET', url))
            .then(onSeriesPhotoTick)
          // Maybe we should do something different on errors (but what?)
            .catch(onSeriesPhotoTick)
        }
      })
      onSeriesPhotoAdd(addCount)
    }
    // now kick off the next series
    if (theTick && theIds[theTick[0]]) {
      setActiveSeriesId(theIds[theTick[0] + 1])
      setSeriesTick([theTick[0] + 1, theTick[1]])
    }
    else {
      // We are done the series
    }
  }

  const finalState = useMemo(() => online ? toolState : STATES.OFFLINE, [online, toolState])

  const geoSeriesQuery = useMemo(() => <GeoQuery bounds={bounds} setSeriesIds={setSeriesIds} />, [bounds])
  const wholeSeriesQuery = useMemo(() => <WholeSeriesConnect id={activeSeriesId} onDone={onSeriesTick} />, [activeSeriesId])

  const onStart = (bounds, viewport) => {
    setToolState(STATES.DOWNLOADING)
    setBounds(bounds)
    setOfflineViewport(viewport)
  }

  const onDone = () => {
    setToolState(STATES.LOCKED)
    setOfflineCompleted(true)
  }

  const onCancel = () => {
    setToolState(STATES.CANCELED)
    setBounds(null)
    setSeriesIds(null)
    setTileTick([0, 0])
    setSeriesTick([0, 0])
    setSeriesPhotoTick([0, 0])
    setSeriesPhotos([])
    limiterRef.current.stop({ dropWaitingJobs: true })
    limiterRef.current = null
    limiterRef.current = new Bottleneck({ maxConcurrent: 5 })
  }

  // Initialize the bottleNeck
  useEffect(() => {
    limiterRef.current = new Bottleneck({ maxConcurrent: 5 })
  }, [])

  // This is a little taxing so we keep it async
  useEffect(() => {
    if (bounds) {
      const tiles = getTiles(bounds, [0, home.Offline.highestOfflineTileZoom])
      const length = tiles.length
      if (length === 0) return
      onTileAdd(length)
      tiles.forEach(tile => {
        limiterRef.current.schedule(() => makeRequest('GET', tile))
          .then(onTileTick)
          // Maybe we should do something different on errors (but what?)
          .catch(onTileTick)
      })
    }
  }, [bounds])

  // If we see series then kick off that cascade
  useEffect(() => {
    // console.log('OfflineMap:: kicking off the series loading', { seriesTick, seriesIds })
    if (seriesIds && seriesIds.ids) {
      // Kick off the series loading
      if (seriesIds.ids.length > 0) setActiveSeriesId(seriesIds.ids[0])
      setSeriesTick([0, seriesIds.ids.length])
      setSeriesPhotoTick([0, 0])
    }
  }, [seriesIds])

  useEffect(() => {
    const newTotal = [tileTick[0] + seriesTick[0] + seriesPhotoTick[0], tileTick[1] + seriesTick[1] + seriesPhotoTick[1]]
    if (newTotal[1] > 0 && newTotal[0] === newTotal[1]) onDone()
  }, [tileTick, seriesTick, seriesPhotoTick])

  return (
    <React.Fragment>
      {wholeSeriesQuery}
      {geoSeriesQuery}
      <OfflineMap
        onMenuBack={() => {
          onCancel()
          onMenuBack()
        }}
        onStartDownload={onStart}
        onCancel={onCancel}
        initViewport={viewport || lastMapViewport}
        progress={{ tileTick, seriesTick, seriesPhotoTick }}
        toolState={finalState}

        onCancelComplete={() => {
          setToolState(STATES.NAVIGATING)
        }}
        onUnlock={() => {
          setToolState(STATES.NAVIGATING)
          setOfflineCompleted(false)
        }}
      />
    </React.Fragment>
  )
}
OfflineMapConnect.propTypes = {
  // All from redux
  onMenuBack: PropTypes.func.isRequired,
  lastMapViewport: PropTypes.object,
  completedDownload: PropTypes.bool,
  viewport: PropTypes.object,
  setOfflineCompleted: PropTypes.func.isRequired,
  setOfflineViewport: PropTypes.func.isRequired
}

const withGeoSearch = graphql(geoSeriesIdOnly, {
  props: ({ data, ownProps }) => {
    if (data.error) console.error(data.error)
    console.log('OfflineMap::withGeoSearch:: ', { data, ownProps })
    try {
      // Fetch until we have everything
      if (!data.loading && !data.error && data.seriesInGeoBounds && data.seriesInGeoBounds.seriesSummaries.length > 0 && data.seriesInGeoBounds.nextToken) {
        ownProps.setSeriesIds({ ids: [], loading: true })
        // console.log('OfflineMap::withGeoSearch::fetchMore', { ...data.variables, nextToken: data.seriesInGeoBounds.nextToken })
        data.fetchMore({
          variables: { ...data.variables, nextToken: data.seriesInGeoBounds.nextToken },
          // How do we mush the new query with the old one
          updateQuery: (prev, { fetchMoreResult }) => {
            // console.log('OfflineMap::withGeoSearch::updateQuery', { prev, fetchMoreResult })
            return {
              seriesInGeoBounds: {
                ...fetchMoreResult.seriesInGeoBounds,
                seriesSummaries: [ ...prev.seriesInGeoBounds.seriesSummaries, ...fetchMoreResult.seriesInGeoBounds.seriesSummaries ]
              }
            }
          }
        })
      }
      else if (data.loading || data.error) {
        // console.log('OfflineMap::withGeoSearch::loading or errir')
        ownProps.setSeriesIds({ ids: [], loading: true })
      }
      else {
        console.log('OfflineMap::withGeoSearch::DONE')
        if (data && data.seriesInGeoBounds) {
          ownProps.setSeriesIds({ ids: data.seriesInGeoBounds.seriesSummaries.map(s => s.id), loading: false })
        }
      }
    }
    catch (e) {
      console.error(data)
      console.error(e)
    }
    return {
      data,
      loading: data.loading
    }
  },
  skip: ({ bounds }) => !bounds,
  options: ({ bounds }) => ({
    variables: {
      bounds,
      limit: 100
      // nextToken
    }
  })
})

/**
 * Here's the graphql HOC that gets an individual series
 */
const withGetSelectedQuery = graphql(getWholeSeries, {
  skip: ({ id }) => !id,
  props: ({ data, ownProps }) => {
    if (data.error) console.error(data.error)
    // console.log('OfflineMap::withGetSelectedQuery:: ', { data })
    let series = null
    try {
      if (!data.loading && !data.error) {
        series = data.series
        // console.log('OfflineMap::withGetSelectedQuery::series', series)
        const urls = []
        urls.push(series.createdUser.gravatar)
        urls.push(series.updatedUser.gravatar)
        series.photos.forEach(ph => {
          urls.push(ph.createdUser.gravatar)
          urls.push(ph.updatedUser.gravatar)
          // Skip medium for size sake
          home.Offline.seriesImgsToCache
            .map(size => urls.push(`${ph.paths.base}${ph.paths[size]}`))
        })
        // Remove duplicates and send it back
        ownProps.onDone([...new Set(urls)])
      }
    }
    catch (e) {
      console.log('OfflineMap::withGetSelectedQuery:: error', { data, e })
      console.error(e)
    }
    return {
      data,
      loading: data.loading,
      series
    }
  },
  options: ({ id }) => ({
    variables: {
      id
    }
  })
})
// We're using graphql() HOC because it's convenient but it doesn't need to render anything
const WholeSeriesConnect = withGetSelectedQuery(() => null)
const GeoQuery = withGeoSearch(() => null)

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const lastMapViewport = state.map.slice(-1).pop()
  const { completedDownload, viewport } = state.offline
  return { lastMapViewport, completedDownload, viewport }
}

const mapDispatchToProps = dispatch => ({
  onMenuBack: () => {
    dispatch(actions.homeStateBack())
  },
  setOfflineCompleted: (bVal) => {
    dispatch(actions.setOfflineCompleted(bVal))
  },
  setOfflineViewport: (viewport) => {
    dispatch(actions.setOfflineViewport(viewport))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OfflineMapConnect)

/**
 * Promisify our XMLHttpRequests
 * @param {*} method
 * @param {*} url
 */
function makeRequest (method, url) {
  return new Promise((resolve, reject) => {
    var xhr = new XMLHttpRequest()
    xhr.open(method, url)
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) resolve(xhr.response)
      else reject(new Error(xhr.statusText))
    }
    xhr.onerror = () => {
      reject(new Error(xhr.statusText))
    }
    xhr.send()
  })
}

export const getTileURL = (url, zoom, x, y) => {
  const newUrl = url + `?access_token=${process.env.REACT_APP_MAPBOX_TOKEN}`
  return newUrl.replace('{z}/{x}/{y}', `${zoom}/${x}/${y}`)
}

/**
 * Loop over all the tiles and zoom levels and build
 * us an array of URLs
 * @param {*} bounds
 * @param {*} right
 */
export function getTiles (bounds, zoomRange) {
  const tiles = []

  for (let zoom = zoomRange[0]; zoom <= zoomRange[1]; zoom++) {
    const xtileLeft = long2tile(bounds[0], zoom)
    const xtileRight = long2tile(bounds[2], zoom)

    const ytileTop = lat2tile(bounds[1], zoom)
    const ytileBottom = lat2tile(bounds[3], zoom)

    for (let x = xtileLeft; x <= xtileRight; x++) {
      for (let y = ytileTop; y <= ytileBottom; y++) {
        // Push both tiles
        tiles.push(getTileURL(process.env.REACT_APP_TILEURL_SAT, zoom, x, y))
        tiles.push(getTileURL(process.env.REACT_APP_TILEURL_TOPO, zoom, x, y))
      }
    }
  }
  return tiles
  // console.log('Starting Coords', { tiles, top, left, bottom, right })
}
