// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { getWholeSeriesPhotoUpdate, allSeriesPhotoUpdate, feedUpdate } from './updateQueries'
import { createPhoto, newPhotoKeys } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'

import _ from 'lodash'

const withMutation = graphql(createPhoto, {
  props: ({ mutate, ownProps }) => {
    return {
      createPhotoMutation: (series, newPhoto, ext) => {
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const newPhotoInput = _.cloneDeep(newPhoto)
        if (!newPhotoInput.seriesId && ownProps.activeSeries) {
          newPhotoInput.seriesId = ownProps.activeSeries
        }
        const { id, gravatar, gravatarProfile, username } = ownProps.user
        const meUserSummary = { id, gravatar, gravatarProfile, username, __typename: 'UserSummary' }
        const variables = {
          photo: _.pick(newPhotoInput, newPhotoKeys),
          ext
        }
        const optimisticResponse = createOptimisticResponse({ meUserSummary, series, newPhoto, ext })
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, createPhoto)

        return mutate({
          variables,
          optimisticResponse,
          update: updateThunk()
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('createPhotoMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  getWholeSeriesPhotoUpdate(store, data.createPhoto.photo)
  allSeriesPhotoUpdate(store, context.series, data.createPhoto.photo)
  feedUpdate(store, context.series, data.createPhoto.photo)
}

const createOptimisticResponse = ({ meUserSummary, series, newPhoto, ext }) => ({
  createPhoto: {
    photo: {
      id: newPhoto.id,
      meta: newPhoto.meta || null,
      seriesId: newPhoto.seriesId,
      createdOn: Date.now().toString(),
      createdUser: meUserSummary,
      updatedUser: meUserSummary,
      createdBy: meUserSummary.id,
      updatedBy: meUserSummary.id,
      uploadToken: newPhoto.uploadToken,
      updatedOn: Date.now().toString(),
      seriesCreatedBy: series.createdUser.id, // we can't answer this yet
      takenOn: newPhoto.takenOn,
      coords: newPhoto.coords || null,
      tags: newPhoto.tags || [],
      paths: {
        base: null,
        large: null,
        orig: null,
        thumb: null,
        medium: null,
        __typename: 'Paths'
      },
      __typename: 'Photo'
    },
    preAuth: null,
    __typename: 'NewPhoto'
  }
})

export default {
  withMutation,
  updateThunk
}
