
import React, { useState, useMemo, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
// import Typography from '@material-ui/core/Typography'
import { FlyToInterpolator, LinearInterpolator, Marker } from 'react-map-gl'
import { easeCubic } from 'd3-ease'
import { CircularProgress } from '@material-ui/core'
import MapStyles from '../../mapStyles'

import BaseMap from './BaseMap'
import useClusteredData from '../../lib/useClusteredData'
import { bbox, featureCollection, buffer, point } from '@turf/turf'
import { makeStyles } from '@material-ui/core/styles'
// import { debounce } from '../../lib/util'
import style from './HomeMap.style'

import TopBar from '../TopBar'
import SearchInput from '../Search/SearchInput'
import SeriesListItem from '../misc/SeriesListItem'
import ClusterSelected from '../misc/ClusterSelected'
import ViewListDialog from '../misc/ViewListDialog'

import SingleMarker from './markers/Single'
import ClusterMarker from './markers/Cluster'

const useStyles = makeStyles(style)

const defaultView = {
  zoom: 4,
  latitude: 44.629701,
  longitude: -120.333379
}

const SIZESCALE = 80

const HomeMap = ({
  seriesList, initViewport, loading, activeSeries, queryVersion,
  onSeriesNavigate, onSeriesMapSelect, onSearchClick,
  popMapHistory, pushMapHistory, clearMapHistory
}) => {
  const [mapViewObj, setMapViewObj] = useState({
    viewport: { ...defaultView, ...initViewport.viewport },
    bounds: initViewport.bounds,
    filterBounds: null
  })

  const classes = useStyles()
  const mapViewRef = useRef()
  const [isTransitioning, setIsTransitioning] = useState(false)
  const homeMapContainerRef = useRef()

  const [currentZoom, setCurrentZoom] = useState(0)
  const [showViewList, setShowViewList] = useState(false)
  const [transitions, setTransitions] = useState(null)
  const [selectedItem, setSelectedItem] = useState()

  // This custom hook is heavily debounced
  const { filteredClusteredData } = useClusteredData(
    seriesList || [],
    mapViewObj.filterBounds,
    currentZoom,
    mapViewObj.viewport,
    SIZESCALE,
    queryVersion,
    isTransitioning
  )

  // If we're coming back to the map with something aleady selected then we need to
  // reselect the series we had before
  useEffect(() => {
    if (!activeSeries || !seriesList || selectedItem) return
    try {
      const selected = seriesList.find(s => s.id === activeSeries)
      if (selected && selected.coords) {
        const bufferPts = buffer(point(selected.coords), 10, { units: 'meters' })
        const bounds = bbox(bufferPts)
        setMapViewObj({ ...mapViewObj, bounds })
      }
    }
    catch (e) {
      console.error(e)
    }
  }, [seriesList])

  // When we load data, we should reselect the activeSeries if we need to
  // Once we have the zoom in place we might need to select an initial series
  useEffect(() => {
    if (!activeSeries || !filteredClusteredData.single || selectedItem) return
    try {
      const selected = filteredClusteredData.single.find(s => s.properties.id === activeSeries)
      if (selected && homeMapContainerRef.current) setSelectedItem(selected)
    }
    catch (e) {
      console.error(e)
    }
  }, [filteredClusteredData.single])

  const saveMap = (delay) => {
    const {
      transitionDuration,
      transitionInterpolator,
      transitionInterruption,
      maxZoom,
      minZoom,
      maxPitch,
      minPitch,
      ...newView
    } = mapViewRef.current ? mapViewRef.current : mapViewObj.viewport
    const saveObj = { viewport: newView }
    if (!delay) pushMapHistory(saveObj)
    else {
      setTimeout(() => pushMapHistory(saveObj), delay)
    }
  }

  const updateSelectedItem = (item) => {
    // regardless, set the map selected Item
    setSelectedItem(item)
    // update the global active series if this is a single and not a cluster
    if (item && item.properties.count === 1) onSeriesMapSelect(item.properties.id)
    else onSeriesMapSelect()
    saveMap()
  }

  const onZoomTo = (object) => {
    try {
      const pts = featureCollection(object.properties.points)
      let bounds = bbox(pts)
      // If all points have the same BBOX this won't work
      if (bounds[0] === bounds[2] && bounds[1] === bounds[3]) {
        const paddedPoint = buffer(point([bounds[0], bounds[1]]), 10, { units: 'meters' })
        bounds = bbox(paddedPoint)
      }
      // buffer a further 10%
      const lngBuff = (bounds[2] - bounds[0]) * 0.2
      const latBuff = (bounds[3] - bounds[1]) * 0.2
      const buffBounds = [
        bounds[0] - lngBuff,
        bounds[1] - latBuff,
        bounds[2] + lngBuff,
        bounds[3] + latBuff
      ]

      setTransitions({
        transitionDuration: 2000,
        transitionInterpolator: new FlyToInterpolator(),
        transitionEasing: easeCubic
      })
      setMapViewObj({ ...mapViewObj, bounds: buffBounds })
      if (object.properties.count > 1) setTimeout(() => updateSelectedItem(null), 2000)
    }
    catch (e) {
      console.error(e)
    }
  }

  const onViewportChange = ({ viewport, map }) => {
    mapViewRef.current = viewport
    // mapViewObj
    // {
    //   "_sw": {
    //     "lng": -120.40788003760076,
    //     "lat": 44.609051464438835
    //   },
    //   "_ne": {
    //     "lng": -120.25887796240545,
    //     "lat": 44.65034319161438
    //   }
    // minX, minY, maxX, maxY
    // [W-122.87948116236043, S26.15346805640254, E-82.00799782432676, N47.3795453070757]
    const viewZoom = Math.floor(viewport.zoom)
    if (!isTransitioning && map && map.getBounds) {
      const { _sw: { lng: minLng, lat: minLat }, _ne: { lng: maxLng, lat: maxLat } } = map.getBounds()
      const [oldMinLng, oldMinLat, oldMaxLng, oldMaxLat] = mapViewObj.filterBounds || [minLng, minLat, maxLng, maxLat]
      if (viewZoom !== currentZoom ||
        oldMinLng >= minLng || oldMinLat >= minLat ||
        oldMaxLng <= maxLng || oldMaxLat <= maxLat
      ) {
        const filterBounds = [
          minLng - (maxLng - minLng) / 2,
          minLat - (maxLat - minLat) / 2,
          maxLng + (maxLng - minLng) / 2,
          maxLat + (maxLat - minLat) / 2
        ]
        setMapViewObj({ ...mapViewObj, filterBounds })
      }
    }
    if (viewZoom !== currentZoom) {
      setCurrentZoom(viewZoom)
    }
  }
  // Debounce this to improve performance
  // const onViewportChangeDebounced = useCallback(debounce(onViewportChange, 300), [])

  const lock = {}

  const onMarkerClick = (obj) => {
    // Second click zooms
    if (selectedItem && selectedItem.properties.id === obj.properties.id) {
      onZoomTo(selectedItem)
    }
    else {
      setTransitions({
        transitionDuration: 500,
        transitionInterpolator: new LinearInterpolator(),
        transitionEasing: easeCubic
      })
      setMapViewObj({
        viewport: {
          longitude: obj.geometry.coordinates[0],
          latitude: obj.geometry.coordinates[1]
        }
      })
    }
    updateSelectedItem(obj)
  }

  // This one should be debounced by filteredClusteredData
  const markersRendered = useMemo(() => ([
    ...filteredClusteredData.single
      .filter(obj => !isTransitioning || (selectedItem && selectedItem.properties.id === obj.properties.id))
      .map(obj => (
        <Marker
          className={'singleMarker'}
          key={`single-${obj.properties.id}`}
          latitude={obj.geometry.coordinates[1]}
          longitude={obj.geometry.coordinates[0]}
          offsetLeft={0}
          offsetTop={-20}
        >
          <SingleMarker name={obj.properties.name}
            access={obj.properties.access}
            photoCount={obj.properties.photoCount}
            onClick={() => onMarkerClick(obj)}
            direction={currentZoom > 10 && obj.properties.direction ? obj.properties.direction : null}
            active={selectedItem && selectedItem.properties.id === obj.properties.id}
          />
        </Marker>
      )),
    ...filteredClusteredData.cluster
      .filter(obj => !isTransitioning || (
        selectedItem &&
      selectedItem.properties.id === obj.properties.id &&
      selectedItem.properties.count === obj.properties.count
      ))
      .map(obj => (
        <Marker
          className='clusterMarker'
          key={`cluster-${obj.properties.id}`}
          latitude={obj.geometry.coordinates[1]}
          longitude={obj.geometry.coordinates[0]}
          offsetLeft={-20}
          offsetTop={-20}
        >
          <ClusterMarker
            seriesCount={obj.properties.count}
            photoCount={obj.properties.reducedPhotoCount}
            onClick={() => onMarkerClick(obj)}
            active={
              selectedItem &&
            selectedItem.properties.id === obj.properties.id &&
            selectedItem.properties.count === obj.properties.count
            }
          />
        </Marker>
      ))
  ]), [filteredClusteredData, selectedItem, isTransitioning])

  const selectedItemRender = useMemo(() => !!selectedItem && (
    <Paper className={classes.selectedPopup}>
      {selectedItem.properties.count === 1
        ? <SeriesListItem
          series={selectedItem.properties}
          showCreatedOn
          showLastPhoto
          onClick={() => onSeriesNavigate(selectedItem.properties.id)}
        />
        : <ClusterSelected
          seriesCount={selectedItem.properties.count}
          reducedPhotoCount={selectedItem.properties.reducedPhotoCount}
          onViewList={() => setShowViewList(true)}
          onClose={() => updateSelectedItem(null)}
          onZoomTo={() => onZoomTo(selectedItem)}
        />
      }
    </Paper>
  ), [selectedItem])

  return (
    <div className={classes.root} ref={homeMapContainerRef}>
      <TopBar isTransparent className={classes.topBar}>
        <SearchInput
          isTransparent
          onClick={onSearchClick}
          defaultValue={''}
        />
      </TopBar>
      {loading && (
        <div className={classes.loadingWrapper}>
          <CircularProgress size={80} thickness={7} color='inherit' />
          {/* <Typography variant='h3' color='inherit'>Loading...</Typography> */}
        </div>
      )}

      <BaseMap
        showMe
        // This should only change on INIT or transition events
        viewport={mapViewObj.viewport}
        transitions={transitions}
        mapStyles={MapStyles}
        controlPadding={{ top: 56 }}
        initStyle={MapStyles.length > 1 ? 1 : 0}
        {...lock}
        onTransitionStart={() => {
          setIsTransitioning(true)
        }}
        onTransitionEnd={() => {
          setIsTransitioning(false)
          setTransitions(null)
        }}
        bounds={mapViewObj.bounds}
        maxZoom={20}
        minZoom={1}
        onViewportChange={onViewportChange}
        onNativeClick={() => {
          if (selectedItem) updateSelectedItem(null)
        }}
      >
        {markersRendered}
      </BaseMap>
      {selectedItem && selectedItem.properties.count > 1 && (
        <ViewListDialog
          open={showViewList}
          title={`${selectedItem.properties.count} Series`}
          onClose={() => setShowViewList(false)}
          onSelect={onSeriesNavigate}
          seriesList={selectedItem.properties.points.map(p => p.properties)}
        />
      )}
      {selectedItemRender}
    </div>
  )
}

HomeMap.propTypes = {
  seriesList: PropTypes.array,
  queryVersion: PropTypes.number,
  activeSeries: PropTypes.string,
  initViewport: PropTypes.object,
  loading: PropTypes.bool,
  // These all get loaded up through mapFuncs in HomeMap.connect
  onSearchClick: PropTypes.func,
  onSeriesNavigate: PropTypes.func,
  onSeriesMapSelect: PropTypes.func,
  popMapHistory: PropTypes.func,
  pushMapHistory: PropTypes.func,
  clearMapHistory: PropTypes.func
}

export default HomeMap

