import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { makeStyles } from '@material-ui/core/styles'
import BrokenImg from './BrokenImg'

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    display: 'block',
    height: '100%',
    width: '100%'
  },
  img: {
    display: 'block',
    height: '100%',
    width: '100%'
  },
  imgBroken: {
    display: 'none'
  }
}))

export const RawImage = ({ className, getPhotoUrls, sizePriority, dataUrl, photo, imgRef, cover, style, ...props }) => {
  const classes = useStyles()
  const [imgUrls, setImgUrls] = useState()

  const [altIndex, setAltIndex] = useState(0)
  const [broken, setBroken] = useState(false)

  useEffect(() => {
    if (!dataUrl && photo && getPhotoUrls) {
      getPhotoUrls(photo, ['large', 'medium']).then(setImgUrls)
        .catch(() => {
          setBroken(true)
        })
    }
  }, [photo])

  // If nothing works then show a broken image component
  const finalURl = dataUrl || (imgUrls && imgUrls[altIndex])
  return (
    <React.Fragment>
      {(!dataUrl && (!imgUrls || broken)) && (
        <BrokenImg className={className} loading={!broken} {...props} />
      )}
      {finalURl && (
        <img ref={imgRef} className={classNames(className, broken ? classes.imgBroken : classes.img)}
          alt={sizePriority[altIndex]}
          style={{
            ...style,
            objectFit: cover ? 'cover' : 'contain'
          }}
          onError={() => {
            if (!dataUrl && imgUrls[altIndex + 1]) {
              console.error('RawImage::Setting Alternate image', altIndex)
              // Now that uploading works properly retries are dead
              setAltIndex(altIndex + 1)
            }
            else {
              setBroken(true)
            }
          }}
          // onLoad={onLoad}
          src={finalURl}
          {...props}
        />
      )}
    </React.Fragment>
  )
}

RawImage.propTypes = {
  className: PropTypes.string,
  sizePriority: PropTypes.arrayOf(PropTypes.string),
  imgRef: PropTypes.any,
  getPhotoUrls: PropTypes.func,
  cover: PropTypes.bool,
  dataUrl: PropTypes.string,
  // onLoad: PropTypes.func,
  photo: PropTypes.object,
  style: PropTypes.object
}

export default RawImage
