// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { getWholeSeriesPhotoUpdate, allSeriesPhotoUpdate, feedUpdate } from './updateQueries'
import { updatePhoto, updatePhotoKeys } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'

import _ from 'lodash'

const withMutation = graphql(updatePhoto, {
  props: ({ mutate, ownProps }) => {
    return {
      updatePhotoMutation: (photo, photoDelta) => {
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const newUpdatePhoto = _.pick(photoDelta, updatePhotoKeys)
        if (newUpdatePhoto.tags && newUpdatePhoto.tags.length > 0) {
          newUpdatePhoto.tags = newUpdatePhoto.tags.map(t => {
            const newT = _.pick(t, [ 'label', 'typeId', 'coords' ])
            if (newT.coords) newT.coords = _.pick(newT.coords, [ 'x', 'y' ])
            return newT
          })
        }

        const { id, gravatar, gravatarProfile, username } = ownProps.user
        const meUserSummary = { id, gravatar, gravatarProfile, username, __typename: 'UserSummary' }
        const variables = {
          photo: newUpdatePhoto
        }
        const optimisticResponse = createOptimisticResponse({ meUserSummary, photo, photoDelta })
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, updatePhoto)

        return mutate({
          variables,
          optimisticResponse,
          update: updateThunk({ photo })
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('updatePhotoMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  getWholeSeriesPhotoUpdate(store, { ...context.photo, ...data.updatePhoto })
  allSeriesPhotoUpdate(store, null, { ...context.photo, ...data.updatePhoto })
  feedUpdate(store, null, { ...context.photo, ...data.updatePhoto })

  if (cb) cb(data.createSeries.series.id)
}

const createOptimisticResponse = ({ meUserSummary, photo, photoDelta }) => ({
  updatePhoto: {
    ...photo,
    ...photoDelta,
    updatedUser: meUserSummary,
    updatedBy: meUserSummary.id,
    __typename: 'Photo'
  }
})

export default {
  withMutation,
  updateThunk
}
