import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { home, tagColors } from '../config'
import permissions from '../permissions'
import { graphql, compose } from 'react-apollo'
import { connect } from 'react-redux'
import actions from '../redux/actions'
import { getWholeSeries } from '../graphql/queries'
import createSeries from '../graphql/mutations/createSeries'
import deleteSeries from '../graphql/mutations/deleteSeries'
import updateSeries from '../graphql/mutations/updateSeries'
import deletePhoto from '../graphql/mutations/deletePhoto'
import createPhoto from '../graphql/mutations/createPhoto'
import updatePhoto from '../graphql/mutations/updatePhoto'
import watch from '../graphql/mutations/watch'
import unwatch from '../graphql/mutations/unwatch'
import RepeatablePhotos, { MODES } from '../components/PhotoSeries'
import { AppContext } from '../contexts'
import { imgStore } from '../graphql/offline'
// import { getExtFromBlob } from '../lib/Photo'

import { ThemeProvider } from '@material-ui/styles'
import themes from '../themes'
import { MODES as INPUT_MODES } from '../components/PhotoSeries/views/NewPhoto'

const SeriesConnect = ({
  series, loading, domain, activeSeries, initialPhoto,
  onAfterSeriesDelete, onBack, queueUpload,
  createSeriesMutation, createPhotoMutation,
  updateSeriesMutation, updatePhotoMutation,
  deleteSeriesMutation, deletePhotoMutation,
  watchSeriesMutation, unWatchSeriesMutation,
  onFindOnMap
}) => {
  const { role, currentUser, user, signedIn, isIos, onAvatarClick, getPhotoUrls } = useContext(AppContext)
  let inputMethod = 'camera'
  // Hack until we can get good loading object sorted
  if (domain === home.AppStateTypes.NEW_SERIES_CAMERA) {
    if (isIos) inputMethod = INPUT_MODES.LEGACY_CAMERA
    else inputMethod = INPUT_MODES.CAMERA
  }
  else if (domain === home.AppStateTypes.NEW_SERIES_LIBRARY) inputMethod = INPUT_MODES.FILEPICKER

  // console.log('SeriesConnect', { series, loading, currentState, activeSeries, role, currentUser })
  let modeName = MODES.SERIES_VIEW
  if (domain !== home.AppStateTypes.SERIES) modeName = MODES.NEWSERIES

  // Series config is the object that series needs to know how to process things outside itself (like lookup urls etc.)
  const seriesConfig = {
    meta: {},
    tags: [
      {
        id: 'freeBothType',
        label: null,
        color: tagColors[0],
        allowXY: true,
        allowNonXY: true,
        allowAdditions: true,
        // This one returns a promise
        getValues: () => Promise.resolve(['a', 'b', 'c']),
        imgStore,
        values: []
      }
    ],
    onAvatarClick,
    getPhotoUrls,
    isIos
  }

  let owned = false
  try {
    owned = user.id === series.createdUser.id
  }
  catch (e) {}
  console.log('[SERIESCONNECT]', { activeSeries, series, loading, owned, user })

  return (
    <ThemeProvider theme={themes.series}>
      <RepeatablePhotos
        modeName={modeName}
        inputMethod={inputMethod}

        owned={owned}
        watching={signedIn && user && user.watching && user.watching.indexOf(activeSeries) > -1}

        // Everything we need to determine access
        currentUserId={signedIn ? currentUser.attributes.sub : null}
        currentUserRole={role}
        accessControl={permissions}
        loading={loading}
        series={series}
        initialPhoto={initialPhoto}
        seriesConfig={seriesConfig}

        // Now some functions to handle actions we might take
        onNewPhoto={async (newPhoto, newPhotoData) => {
          console.log('onNewPhoto::', { newPhoto, newPhotoData })
          // Write this blob to the imgStore so we can use it later
          await imgStore.setItem(newPhoto.uploadToken, newPhotoData)
          return createPhotoMutation(series, newPhoto, newPhotoData.ext)
            .then(({ data }) => {
              // Now we can add this photo to the upload queue
              const photo = data.createPhoto.photo
              if (data.createPhoto.preAuth) {
                const preAuth = JSON.parse(data.createPhoto.preAuth)

                queueUpload({
                  id: photo.id,
                  uploadToken: photo.uploadToken,
                  seriesId: photo.seriesId,
                  preAuth
                })
              }
            })
            .catch(err => {
              console.log('onNewPhoto:: Graphql Error', err.stack)
              console.error(err)
            })
        }}
        onChangePhoto={(photo, photoDelta) => updatePhotoMutation(photo, photoDelta)
          .catch(err => {
            console.log('onChangePhoto:: Graphql Error', err.stack)
            console.error(err)
          })
        }
        onDeletePhoto={(photo) => deletePhotoMutation(photo.id)
          .then((...data) => {
            console.log('onDeletePhoto:: photo deleted', data)
          })
          .catch(err => {
            console.log('onDeletePhoto:: Graphql Error', err.stack)
            console.error(err)
          })
        }
        onChange={(series, seriesDelta) => updateSeriesMutation(series, seriesDelta)
          .catch(err => {
            console.log('onChange:: Graphql Error', err.stack)
            console.error(err)
          })
        }
        onNewSeries={async (newSeries, newPhoto, newPhotoData) => {
          console.log('Series.connect:: onNewSeries starting', { newSeries, newPhoto, newPhotoData })
          await imgStore.setItem(newPhoto.uploadToken, newPhotoData)
          return createSeriesMutation(newSeries, newPhoto, newPhotoData.ext)
            .then(async ({ data }) => {
              if (data.createSeries.preAuth) {
                // Now we can add this photo to the upload queue
                const photo = data.createSeries.series.photos[0]
                const preAuth = JSON.parse(data.createSeries.preAuth)

                queueUpload({
                  id: photo.id,
                  uploadToken: photo.uploadToken,
                  seriesId: photo.seriesId,
                  preAuth
                })
              }
            })
            .catch(err => {
              console.log('onNewSeries:: Graphql Error', err.stack)
              console.error(err)
            })
        }}
        onDelete={() => deleteSeriesMutation()
          .catch(err => {
            console.log('onDelete:: Graphql Error', err.stack)
            console.error(err)
          })
        }
        onClose={onBack}
        onFindOnMap={onFindOnMap}
        onWatch={signedIn ? watchSeriesMutation : null}
        onUnWatch={signedIn ? unWatchSeriesMutation : null}
      />
    </ThemeProvider>
  )
}
SeriesConnect.propTypes = {
  domain: PropTypes.string,
  activeSeries: PropTypes.string,
  initialPhoto: PropTypes.string,

  // From Redux
  onAfterSeriesDelete: PropTypes.func,
  queueUpload: PropTypes.func,
  onBack: PropTypes.func,

  // Data from apollo
  series: PropTypes.object,

  // Functions From Apollo
  loading: PropTypes.bool,
  updateSeriesMutation: PropTypes.func,
  updatePhotoMutation: PropTypes.func,
  deleteSeriesMutation: PropTypes.func,
  deletePhotoMutation: PropTypes.func,
  createSeriesMutation: PropTypes.func,
  createPhotoMutation: PropTypes.func,
  watchSeriesMutation: PropTypes.func,
  unWatchSeriesMutation: PropTypes.func,
  onFindOnMap: PropTypes.func
}

/**
 * Here's the graphql HOC that gets an individual series
 */
const withGetSelectedQuery = graphql(getWholeSeries, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)
    console.log('getWholeSeries:: ', { data })
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      console.log('getWholeSeries:: error', { data, e })
      console.error(e)
    }
    return {
      data,
      loading: data.loading,
      series
    }
  },
  skip: ({ activeSeries, isNewSeries }) => !activeSeries || isNewSeries,
  options: ({ activeSeries }) => ({
    variables: {
      id: activeSeries
    }
  })
})

/**
 * Compose all our queries together
 */
const connectedWithApollo = compose(
  withGetSelectedQuery,
  createSeries.withMutation,
  createPhoto.withMutation,
  updateSeries.withMutation,
  updatePhoto.withMutation,
  deleteSeries.withMutation,
  deletePhoto.withMutation,
  watch.withMutation,
  unwatch.withMutation
)(SeriesConnect)

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { current: { activeSeries, initialPhoto, domain } } = state.home
  return { activeSeries, initialPhoto, domain }
}

const mapDispatchToProps = dispatch => ({
  onBack: () => {
    dispatch(actions.homeStateBack())
  },
  onAfterSeriesDelete: (key, newProgress) => {
    dispatch(actions.setHomeState(home.AppStateTypes.FEED))
  },
  queueUpload: (uploadObj) => {
    dispatch(actions.queueUpload(uploadObj))
  },
  doneCreatingSeries: (activeSeries) => {
    console.log('DONECREATINGSERIES', activeSeries)
    dispatch(actions.setHomeState(home.AppStateTypes.SERIES, { activeSeries }))
  },
  onFindOnMap: () => {
    dispatch(actions.setHomeState(home.AppStateTypes.MAP))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(connectedWithApollo)
