import React from 'react'
import PropTypes from 'prop-types'
import Dialog from '@material-ui/core/Dialog'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import SeriesListItem from './SeriesListItem'

import CloseIcon from '@material-ui/icons/Close'
import { makeStyles } from '@material-ui/core/styles'
import { List } from '@material-ui/core'

const style = theme => ({
  root: {

  },
  title: {
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    alignSelf: 'flex-start',
    width: '100%',
    margin: 0,
    display: 'flex',
    padding: theme.spacing(2)
  },
  dlgTitle: {
    flexGrow: 1
  },
  closeButton: {
    padding: 0,
    alignSelf: 'flex-start'
    // color: theme.palette.grey[500]
  },
  list: {
    overflowX: 'hidden',
    overflowY: 'scroll'
  }
})

const useStyles = makeStyles(style)

const ViewListDialog = ({ title, seriesList, onSelect, onClose, open }) => {
  const classes = useStyles()
  return (
    <Dialog
      className={classes.root}
      fullWidth
      open={open}
      onClose={onClose}
      maxWidth='xl'
      aria-labelledby='confirmation-dialog-title'
    >
      <DialogTitle className={classes.title} disableTypography>
        <Typography className={classes.dlgTitle}>{title}</Typography>
        <IconButton color='inherit' className={classes.closeButton} onClick={onClose}>
          <CloseIcon color='inherit' />
        </IconButton>
      </DialogTitle>
      <List className={classes.list} dense disablePadding>
        {seriesList.map(series => (
          <SeriesListItem
            key={`series${series.id}`}
            series={series}
            small
            onClick={() => onSelect(series.id)}
          />
        ))}
      </List>
    </Dialog>
  )
}

ViewListDialog.propTypes = {
  open: PropTypes.bool,
  title: PropTypes.string.isRequired,
  seriesList: PropTypes.array.isRequired,
  onSelect: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}

export default ViewListDialog
