import React, { useState, useRef, useContext, useLayoutEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { makeStyles } from '@material-ui/core/styles'

import useComponentSize from '../../../../lib/useComponentSize'

import { SeriesContext } from '../../contexts'
import XYTagChip from '../tags/XYTagChip'
import useXYTags from '../tags/useXYTags'
import RawImage from './RawImage'
import style from './Photo.style'

const useStyles = makeStyles(style)

const Photo = ({ photo, opacity, edit, showTags, dataUrl, transition, onChange, className }) => {
  const classes = useStyles()
  const refImgWrap = useRef()
  const imgRef = useRef()
  const [newSize, setNewSize] = useState({ multiplier: 1 })
  const { seriesConfig } = useContext(SeriesContext)

  const { size: { height, width }, setResizeActive } = useComponentSize(refImgWrap)

  // All tag-related info is put in its own hook
  const { onAddTagTrigger, TagChooser, getRelXYTags, onTagDragOver, onTagDragStop, onTagDelete } = useXYTags(photo, { height, width }, onChange, setResizeActive)

  const handleNewSize = () => {
    console.log('handleImageLoaded')
    const { naturalHeight, naturalWidth } = imgRef.current
    // 4 different cases of orientation:
    const multiplier = Math.min(
      height / naturalHeight,
      width / naturalWidth
    )

    const newWidth = multiplier * naturalWidth
    const newHeight = multiplier * naturalHeight
    setNewSize({ newWidth, newHeight, naturalWidth, naturalHeight, multiplier })
  }
  useLayoutEffect(() => {
    if (imgRef.current) handleNewSize()
  }, [width, height])

  return (
    <div className={classNames(classes.root, className)}
      style={{ opacity: opacity !== null ? opacity : 1 }}
      ref={refImgWrap}
    >
      <div className={classes.inner}
        style={{
          height: newSize ? newSize.newHeight : 1,
          width: newSize ? newSize.newWidth : 1
        }}
        onClick={onAddTagTrigger}
      >
        { showTags || edit
          ? (
            <div className={className}>
              { getRelXYTags(newSize.multiplier).map((tag, idx) => (
                <XYTagChip
                  key={`xytag-${idx}`}
                  color={tag.color}
                  {...{ edit, newSize, tag, onTagDragOver, onTagDragStop, onTagDelete }} />
              ))}
            </div>
          ) : null
        }
        <RawImage
          className={classes.rawImage}
          sizePriority={['large', 'medium', 'thumb']}
          photo={photo}
          imgRef={imgRef}
          dataUrl={dataUrl}
          getPhotoUrls={seriesConfig.getPhotoUrls}
          style={{ height: newSize.newHeight, width: newSize.newWidth }}
        />

      </div>
      {edit && <TagChooser />}
    </div>
  )
}

Photo.propTypes = {
  className: PropTypes.string,
  opacity: PropTypes.number,
  photo: PropTypes.object.isRequired,
  dataUrl: PropTypes.string,
  edit: PropTypes.bool,
  showTags: PropTypes.bool,
  transition: PropTypes.string,
  onChange: PropTypes.func
}

export default Photo
