import React from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'

import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'
import ViewListIcon from '@material-ui/icons/ViewList'

import { makeStyles } from '@material-ui/core/styles'
import ListItemIcon from '@material-ui/core/ListItemIcon'

const style = theme => ({
  root: {

  },
  listItemText: {
    textAlign: 'center'
  },
  iconButton: {
    background: theme.palette.grey[200]
  },
  compoundButton: {
    textAlign: 'center'
  }
})
const useStyles = makeStyles(style)

const ClusterSelected = ({ seriesCount, reducedPhotoCount, onZoomTo, onViewList, onClose }) => {
  const classes = useStyles()
  console.log('cluster selected', { seriesCount, reducedPhotoCount })
  return (
    <List>
      <ListItem className={classes.root}>
        <ListItemIcon>
          <div className={classes.compoundButton}>
            <IconButton onClick={onZoomTo} className={classes.iconButton}>
              <ZoomOutMapIcon />
            </IconButton>
            <Typography variant='caption' component='div' >Zoom In</Typography>
          </div>
        </ListItemIcon>
        <ListItemText
          className={classes.listItemText}
          onClick={onZoomTo}
          primary={`${seriesCount} Photo series`}
          secondary={`containing ${reducedPhotoCount} individual photo${reducedPhotoCount === 1 ? '' : 's'}`}
        />
        <ListItemIcon>
          <div className={classes.compoundButton}>
            <IconButton onClick={onViewList} className={classes.iconButton} >
              <ViewListIcon />
            </IconButton>
            <Typography variant='caption' component='div' >List</Typography>
          </div>
        </ListItemIcon>
      </ListItem>
    </List>
  )
}

ClusterSelected.propTypes = {
  seriesCount: PropTypes.number.isRequired,
  reducedPhotoCount: PropTypes.number.isRequired,
  onZoomTo: PropTypes.func.isRequired,
  onViewList: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}

export default ClusterSelected
