// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { allSeriesPhotoUpdate, feedUpdate } from './updateQueries'
import { deletePhoto } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'

const withMutation = graphql(deletePhoto, {
  props: ({ mutate, ownProps }) => {
    return {
      deletePhotoMutation: (photoId) => {
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const variables = {
          seriesId: ownProps.series.id,
          photoId
        }
        const optimisticResponse = {
          deletePhoto: photoId
        }
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, deletePhoto)

        return mutate({
          variables,
          optimisticResponse,
          update: updateThunk({ seriesId: ownProps.series })
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('deletePhotoMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  allSeriesPhotoUpdate(store, null, { id: data.deletePhoto }, true)
  feedUpdate(store, context.series, { id: data.deletePhoto, seriesId: context.series.seriesId }, true)
}

export default {
  withMutation,
  updateThunk
}
