import { Actions } from '../actions'

const offlineInit = {
  completedDownload: false,
  viewport: null
}

export default (state = offlineInit, action) => {
  const { type, data } = action

  switch (type) {
    case Actions.OFFLINE_VIEWPORT_SAVE:
      return {
        ...state,
        viewport: data
      }

    case Actions.OFFLINE_SET_STATUS:
      return {
        ...state,
        completedDownload: data
      }

    default:
      return state
  }
}
