const CAMERAWIDTH = 400
const CAMERAHEIGHT = 400

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    height: '100%',
    flexDirection: 'column'
  },
  qrIcon: {
    border: '5px solid red',
    padding: theme.spacing(2),
    position: 'absolute',
    color: 'white',
    opacity: 0.4,
    zIndex: 2,
    width: '70%',
    height: '70%',
    top: '50%',
    left: '50%',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  },
  backupText: {
    position: 'absolute',
    color: 'white',
    textAlign: 'center',
    zIndex: 2,
    width: '70%',
    height: '70%',
    top: '50%',
    left: '50%',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  },
  manualImg: {
    position: 'absolute',
    zIndex: 1
  },
  appBar: {

  },
  inner: {
    padding: theme.spacing(2),
    height: '100%',
    flexGrow: 1,
    overflow: 'hidden'
  },
  tipPaper: {
    padding: theme.spacing(2),
    margin: theme.spacing(2)
  },
  docContainer: {
    flexGrow: 1,
    padding: theme.spacing(2)
  },
  cameraContainer: {
    width: '80%',
    margin: '0 auto',
    position: 'relative',
    // this is a CSS trick: https://spin.atomicobject.com/2015/07/14/css-responsive-square/
    '&::before': {
      content: ' ',
      display: 'block',
      paddingTop: '100%'
    }
  },
  cameraContainerError: {
    background: 'black'
  },
  snackErr: {
    background: red[500],
    color: 'white'
  },
  snackSuccess: {
    background: green[500],
    color: 'white'
  },
  snackMsg: {

  },
  video: {
    height: '100%',
    width: '100%',
    verticalAlign: 'top'
    // display: 'none'
    // position: 'absolute'
    // top: 0,
    // left: 0,
    // zIndex: 1
    // '@media (orientation: portrait)': {
    //   transform: 'rotate(90)'
    // }
  }
}))

const QRReader = ({ onMenuBack, onCapture, isIos }) => {
  const classes = useStyles()
  const [capturedBlob, setCapturedBlob] = useState()

  // This is only used for legacy capture
  const [err, setErr] = useState(null)
  const [code, setCode] = useState(null)
  const { cameraError, captureRawData, cameraOnline } = useCamera(setCapturedBlob, CAMERAWIDTH, CAMERAHEIGHT)

  // limit to 10 frames per second
  const captureFunc = useCallback(debounce(captureRawData, 100), [cameraOnline, cameraError])

  useEffect(() => {
    const scan = async () => {
      let scannedCode = null
      try {
        scannedCode = await jsQRFromBlob(capturedBlob)
      }
      catch (e) {
        console.error(e)
      }
      try {
        // If we're capturing images manually then we do this every time
        if (cameraError && !scannedCode) throw new Error('Captured Image does not have a QR code')
        if (scannedCode) {
          console.log('DEBUG', scannedCode)
          const find = '?fromqr='
          const foundIdx = scannedCode.data.indexOf(find) + find.length
          const newData = scannedCode.data.slice(foundIdx)
          const decodedUrl = JSON.parse(decodeURI(newData))
          if (!decodedUrl.type || !decodedUrl.id) throw new Error('Type or ID missing from QR Code')
          setCode(decodedUrl)
          setErr(null)
          console.log('QRCODE', scannedCode)
        }
      }
      catch (e) {
        setErr('Not a valid QR Code')
        console.error(e)
      }
    }
    if (cameraOnline && !cameraError && !err && !code) {
      if (!capturedBlob || !capturedBlob.data) captureFunc()
      else {
        scan()
        captureFunc()
      }
    }
  }, [cameraOnline, capturedBlob, err, code])

  // console.log(capturedImg)
  return (
    <div className={classes.root}>
      {/* Render the file picker if there is an error */}

      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <AppBar position='static' className={classes.appBar}>
        <Toolbar disableGutters>
          <IconButton className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant='subtitle1' color='inherit' className={classes.toolbarTitle}>
            Capture QR Code
          </Typography>
        </Toolbar>
      </AppBar>
      <div className={classes.inner}>
        <Paper
          className={classNames(classes.cameraContainer, cameraError ? classes.cameraContainerError : null)}
        >
          <QRcodeIcon className={classes.qrIcon} />
          {/* {cameraError && <Typography className={classes.backupText}>Click to capture</Typography>} */}
          {/* {cameraError && capturedBlob && (
            <RawImg
              dataUrl={capturedBlob}
              className={classes.manualImg}
              // style={{ maxHeight: 'inherit', objectFit: 'contain' }}
            />
          )} */}
          <video
            autoPlay
            playsInline
            muted
            id='webcam'
            className={classes.video}
          />
        </Paper>
        <Paper className={classes.tipPaper}>
          <Typography>TIP: You can find the QR code for a series in the SHARE tab of that series</Typography>
        </Paper>
      </div>
      <Snackbar
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        open={!!err}
        autoHideDuration={2000}
        onClose={() => {
          setErr(null)
          setCapturedBlob(null)
        }}
      >
        <SnackbarContent
          className={classes.snackErr}
          message={<span className={classes.snackMsg}>{err}</span>}
        />
      </Snackbar>
      {!isIos && (
        <Snackbar
          anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
          open={cameraError}
        >
          <SnackbarContent
            className={classes.snackErr}
            message={<span className={classes.snackMsg}>Camera Error. Check your device's permissions</span>}
          />
        </Snackbar>
      )}
      <Snackbar
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        open={!!code}
        autoHideDuration={500}
        onClose={() => onCapture(code.type, code.id)}
      >
        <SnackbarContent
          className={classes.snackSuccess}
          message={<span className={classes.snackMsg}>Valid QR Code found!</span>}
        />
      </Snackbar>

    </div>
  )
}

QRReader.propTypes = {
  onMenuBack: PropTypes.func.isRequired,
  isIos: PropTypes.bool,
  onCapture: PropTypes.func.isRequired
}

export default QRReader

const jsQRFromBlob = ({ data, imageHeight, imageWidth }) => {
  return new Promise((resolve, reject) => {
    try {
      const qrCode = jsQR(data, imageWidth, imageHeight)
      resolve(qrCode)
    }
    catch (e) {
      reject(e)
    }
  })
}
