import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'
import BrokenImageIcon from '@material-ui/icons/BrokenImage'

import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    padding: theme.spacing(2),
    display: 'flex',
    flexDirection: 'column',
    overflow: 'hidden',
    opacity: 0.3,
    textAlign: 'center',
    color: theme.palette.getContrastText(theme.palette.text.primary),
    background: theme.palette.text.primary
  },
  spacer: {
    flexGrow: 1
  },
  icon: {
    width: '100%',
    flex: '1 1 40%',
    opacity: 0.5
  },
  circularProgress: {
    margin: '0 auto'
  },
  primary: {
    textAlign: 'center',
    width: '100%'
  },
  secondary: {
    flex: '1 1 30%',
    width: '100%'
  }
}))

const BrokenImg = ({ loading, className, ...props }) => {
  const classes = useStyles()
  return (
    <div className={classNames(classes.root, className)} {...props}>
      <div className={classes.spacer} />
      {loading
        ? <CircularProgress color='inherit' className={classes.circularProgress} />
        : <BrokenImageIcon className={classes.icon} color='inherit' />
      }
      <Typography className={classes.primary} color='inherit'>
        {loading ? 'Loading' : 'Photo not found' }
      </Typography>
      <div className={classes.spacer} />
    </div>
  )
}
BrokenImg.propTypes = {
  loading: PropTypes.bool,
  className: PropTypes.string
}

export default BrokenImg
