import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import CssBaseline from '@material-ui/core/CssBaseline'
import Snackbar from '@material-ui/core/Snackbar'
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab'
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'

import { makeStyles } from '@material-ui/core/styles'

import RawImage from './subViews/RawImage'
import PhotoGrid from './subViews/PhotoGrid'
import PhotoFadeSlider from './controls/PhotoFadeSlider'
import style from './PhotoReviewTool.style'

import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'

import { SeriesContext } from '../contexts'
const useStyles = makeStyles(style)

const PhotoReviewTool = ({
  captured, defaultRefPhotoId,
  onConfirm, onCancel, onRetry, dataUrl
}) => {
  const { series, seriesConfig } = useContext(SeriesContext)
  const photos = series && series.photos ? series.photos : null
  const classes = useStyles()

  const newRefPhoto = defaultRefPhotoId
    ? photos.find(p => p.id === defaultRefPhotoId)
    : photos && photos.length > 0 ? photos[0] : null

  const [refPhoto, setRefPhoto] = useState(newRefPhoto)
  const [drawerOpen, setDrawerOpen] = useState(false)
  const [slider, setSlider] = useState(100)

  const handleTileClick = (id) => {
    setRefPhoto(series.photos.find(p => p.id === id))
    setDrawerOpen(false)
  }

  if (!captured) return 'Loading...'

  return (
    <div className={classes.root}>
      <CssBaseline />
      { Boolean(refPhoto) && (
        <PhotoFadeSlider defaultValue={100} onChange={setSlider} />
      )}

      <div className={classes.captured}>
        <RawImage
          sizePriority={['large', 'medium', 'thumb']}
          style={{ opacity: slider / 100 }}
          dataUrl={dataUrl}
          photo={captured}
        />
      </div>
      { Boolean(refPhoto) && (
        <div className={classes.reference} >
          <RawImage
            sizePriority={['large', 'medium', 'thumb']}
            getPhotoUrls={seriesConfig.getPhotoUrls}
            photo={refPhoto}
          />
        </div>
      )}
      <Snackbar
        open
        autoHideDuration={null}
        ContentProps={{
          'aria-describedby': 'snackbar-fab-message-id',
          className: classes.snackbarContent
        }}
        message={<span id='snackbar-fab-message-id'>Keep this photo?</span>}
        action={<div>
          <Button color='inherit' size='small' onClick={onCancel}>
                Cancel
          </Button>
          {Boolean(onRetry) && (
            <Button color='inherit' size='small' onClick={onRetry}>
          Retry
            </Button>
          )}

          <Button color='inherit' size='small' onClick={onConfirm}>
                Keep
          </Button>
        </div>
        }
        className={classes.snackbar}
      />

      {series && series.photos && (
        <Fab
          aria-label='library'
          className={classNames(classes.libraryFab, classes.buttonFab)}
          classes={{
            disabled: classes.disabledFab
          }}
          disabled={!series || !series.photos || series.photos.length === 0}
          onClick={() => setDrawerOpen(true)}
        >
          <PhotoLibraryIcon className={classes.libraryIcon} />
        </Fab>
      )}

      {series && series.photos && (
        <SwipeableDrawer
          anchor='left'
          open={drawerOpen}
          classes={{
            paperAnchorLeft: classes.swipeDrawer
          }}
          onClose={() => setDrawerOpen(false)}
          onOpen={() => setDrawerOpen(true)}
        >
          <div
            tabIndex={0}
            className={classes.swipeDrawerContainer}
            role='button'
            onClick={() => setDrawerOpen(false)}
            onKeyDown={() => setDrawerOpen(false)}
          >
            <PhotoGrid
              multiSelect={false}
              columns={1}
              photos={series.photos}
              onTileClick={handleTileClick}
            />
          </div>
        </SwipeableDrawer>
      )}
    </div>
  )
}

PhotoReviewTool.propTypes = {
  defaultRefPhotoId: PropTypes.string,
  captured: PropTypes.object,
  dataUrl: PropTypes.string.isRequired,
  // Now some functions
  onConfirm: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  onRetry: PropTypes.func // only for camera

}

export default PhotoReviewTool
