// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { getWholeSeriesUpdate, allSeriesUpdate, feedUpdate } from './updateQueries'
import { updateSeries, updateSeriesKeys } from '../mutations'
import { pushMutationPromise, popMutationPromise } from '../offline'

import _ from 'lodash'

export const withMutation = graphql(updateSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      updateSeriesMutation: (series, seriesDelta) => {
        const { id, gravatar, gravatarProfile, username } = ownProps.user
        const meUserSummary = { id, gravatar, gravatarProfile, username, __typename: 'UserSummary' }

        const variables = {
          series: _.pick(seriesDelta, updateSeriesKeys)
        }
        const optimisticResponse = createOptimisticResponse({ meUserSummary, series, seriesDelta })
        const mutationStackId = pushMutationPromise({ variables, optimisticResponse }, updateSeries)

        return mutate({
          variables,
          // refetchQueries: [{ query: getWholeSeries, variables: { id: newSeries.id } }],
          optimisticResponse,
          update: updateThunk()
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('updateSeriesMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  getWholeSeriesUpdate(store, data.updateSeries)
  allSeriesUpdate(store, data.updateSeries)
  feedUpdate(store, data.createSeries.series, data.createdSeries.series.photos[0])
}

const createOptimisticResponse = ({ meUserSummary, series, seriesDelta }) => ({
  updateSeries: {
    ...series,
    ...seriesDelta,
    updatedUser: meUserSummary,
    updatedBy: meUserSummary.id,
    __typename: 'Series'
  }
})

export default {
  withMutation,
  updateThunk
}
